
import XHeader from 'vux/src/components/x-header/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import mixins from 'assets/js/mixins.js'
export default {
	name: 'appointment',
	mixins: [mixins.scroller],
	data() {
		return {
			state: {
				0: 'warn', //未就诊
				1: 'success', //已就诊
				2: 'overdue', //过期
				3: 'cancel' //已取消
			},
			visitState: [{
				name: '全部',
				value: ''
			}, {
				name: '未就诊',
				value: 0
			}, {
				name: '已就诊',
				value: 1
			}, {
				name: '过期',
				value: 2
			}, {
				name: '已取消',
				value: 3
			}],
			visitValue: [],
		}
	},
	computed: {
		ajaxData() {
			return {
				header: {
					action: 'GetAppointmentList',
				},
				body: {
					UserID: this.$common.getLocalStorage('user').userid,
					State: this.visitValue[0]
				}
			}
		}
	},
	methods: {
		//取消预约
		deleteAppointment(opt, index) {
			this.$vux.confirm.show({
				title: '提示',
				content: '确认取消预约？',
				onConfirm: () => {
					this.$common.ajax({
						data: {
							header: {
								action: 'DeleteAppointment',
							},
							body: {
								AppointmentID: opt.ID
							}
						},
						success: (res) => {
							this.list.splice(index, 1);
						}
					})

				}
			})

		}
	},
	mounted() {},
	components: {
		XHeader,
		Spinner,
		PopupPicker,
	}
}



